import "./styles.css";
import React, { useState } from "react";
import Container from "./Container";
export default function App() {
  const [userInput, setUserInput] = useState("");
  const [bookInfo, setBookInfo] = useState("");
  const [increase, setIncrease] = useState(0);
  function handleChange(event) {
    //grabs the value of the input box, and saves it into value(state var)
    setUserInput(event.target.value);
    console.log(userInput);
  }
  const handleSubmit = (event) => {
    //creates an object when submit is clicked
    if (userInput !== "") {
      fetch("https://www.googleapis.com/books/v1/volumes?q=" + userInput)
        .then((response) => response.json())
        .then((data) => {
          setBookInfo(data);
          console.log(data.items.length);
          console.log(data);
        });
    } else {
      alert("No result");
    }
    event.preventDefault();
    //setUserInput("");
  };
  function next() {
    if (userInput !== "") {
      if (bookInfo.items.length - 1 === increase) {
        setIncrease(0);
      } else {
        setIncrease(increase + 1);
      }
      console.log(increase);
    }
  }
  return (
    <>
      <div className="submitRequest">
        <div>
          <form onSubmit={handleSubmit}>
            <label>
              <input
                placeholder="BookName"
                type="text"
                onChange={handleChange}
                value={userInput}
              />
            </label>
            <input id="button" type="submit" value="search" />
          </form>
        </div>
        <button onClick={next}>Next</button>
      </div>
      {bookInfo && <Container bookInfo={bookInfo.items[increase]} />}
    </>
  );
}
