import React, { useState } from "react";
function Container({ bookInfo }) {
  const [toggle, setToggle] = useState(false);
  const volumeInfo = bookInfo && bookInfo.volumeInfo;
  const volume = bookInfo.volumeInfo;
  const exist1 = bookInfo.volumeInfo.authors;
  const exist2 = volume.averageRating;
  const exist3 = volume.categories;
  const exist4 = volume.pageCount;
  const exist5 = bookInfo.volumeInfo.publisher;
  const exist6 = bookInfo.volumeInfo.publishedDate;
  let exist =
    bookInfo.saleInfo &&
    bookInfo.saleInfo.listPrice &&
    bookInfo.saleInfo.buyLink;
  return (
    <div className="main flex">
      {volumeInfo && (
        <div>
          <h1 style={{ textAlign: "center", backgroundColor: "#fffdfd" }}>
            {volume.title}
          </h1>
          <div className="flex">
            <div className="image">
              <img
                width="300px"
                height="300px"
                src={volume.imageLinks.thumbnail}
              ></img>
            </div>
            <div>
              <h3>Description:</h3>
              <p>{volume.description}</p>
            </div>
          </div>
          <div style={{ textAlign: "center" }}>
            {exist1 && <h4>Author: {bookInfo.volumeInfo.authors[0]}</h4>}
            {exist5 && <h4>Publisher: {bookInfo.volumeInfo.publisher}</h4>}
            {exist6 && (
              <h4>Publish date: {bookInfo.volumeInfo.publishedDate}</h4>
            )}
          </div>
          <button
            className="ToggleButton"
            style={{ color: "Black", cursor: "pointer" }}
            onClick={() => setToggle(!toggle)}
          >
            Learn more
          </button>
          {toggle && (
            <div style={{ backgroundColor: "snow" }}>
              {exist2 && <p>Rating: {volume.averageRating}</p>}
              {exist3 && <p>Genre: {volume.categories[0]}</p>}
              {exist4 && <p>Length: {volume.pageCount}</p>}
              {exist && (
                <p>
                  Price: {bookInfo.saleInfo.listPrice.amount} at{" "}
                  <a href={bookInfo.saleInfo.buyLink}>Click here</a>
                </p>
              )}
            </div>
          )}
        </div>
      )}
    </div>
  );
}

export default Container;
